import uQRCode from '@/common/uqrcode.js'
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
export default {
	components: {
		uniNavBar,
	},
	data(){
		return {
			// providerList:[],				
			// sourceLink: 'http://yunzhujiao.cn/bind_pub/index.html',		
			// type:0,
			qrcodeText: 'example text message.',
			qrcodeSize: 215,
			qrcodeSrc: '',
			phoneData: '15989584526'
			
		}
	},
	onLoad() {
		this.initialize();
		this.version = plus.runtime.version;
		uni.getProvider({
			service: 'share',
			success: (e) => {
				let data = [];
				for (let i = 0; i < e.provider.length; i++) {
					switch (e.provider[i]) {
						case 'weixin':
							data.push({
								name: '分享到微信好友',
								id: 'weixin'
							})
							data.push({
								name: '分享到微信朋友圈',
								id: 'weixin',
								type: 'WXSenceTimeline'
							})
							break;
						case 'qq':
							data.push({
								name: '分享到QQ',
								id: 'qq'
							})
							break;
						default:
							break;
					}
				}
				this.providerList = data;
			},
			fail: (e) => {
				console.log('获取登录通道失败'+ JSON.stringify(e));
			}
		});

	},
	methods:{
		back() {
			uni.navigateTo({
				url:'infor'
			})
		},
		initialize() {
			uni.request({
				//url:'http://127.0.0.1:8000/do_qrcode/',
				url:'http://39.106.209.123:8000/do_qrcode/',
				data:{
					phoneNumber:this.phoneData,
				},
				/*
				这里其实应该传一个用户登录的信息
				到数据库里去找对应的信息
				但是我暂时没研究清楚该怎么写，就先写死了电话QAQ
				*/
				head:{
					'content-type': 'application/x-www-form-urlencoded'
				},
				method:'POST',
				success: (res) => {
					console.log(res.data);
					this.qrcodeText = res.data.text;
					this.make();
				},
				fail: () => {
					console.log(res.data);
					uni.showToast({
						icon:'none',
						duration:3000,
						title:'请检查网络链接！'
					})
				}
			});
		},
		make() {
			uQRCode.make({
				canvasId: 'qrcode',
				text: this.qrcodeText,
				size: 215,
				margin: 10,
				success: res => {
					console.log(res)
					this.qrcodeSrc = res
				}
			})
		},
		sharurl(){
			uni.setClipboardData({
				data: 'http://sishuquan.com?id=3228969',
				success: function () {
					console.log('success');
					uni.showModal({
						title: '复制成功',
						content: '内容已复制到粘贴板，可前往其他应用粘贴查看。', 
						showCancel:false,
						success: function(res) {
							if (res.confirm) {											 
								//console.log('用户点击确定');
							} else if (res.cancel) {
								//console.log('用户点击取消');
							}
						}
					});
				}
			});
		},
		//保存图片到相册
		save(){
			uni.showActionSheet({
				itemList:['保存图片到相册'],
				success: () => {
					plus.gallery.save(this.qrcodeSrc, function() {
						uni.showToast({
							title:'保存成功',
							icon:'none'
						})
					}, function() {
						uni.showToast({
							title:'保存失败，请重试！',
							icon:'none'
						})
					});
				}
			})
		},
		share(e) {
			if (this.providerList.length === 0) {
				uni.showModal({
					title: '当前环境无分享渠道!',
					showCancel: false
				})
				return;
			}
			let itemList = this.providerList.map(function (value) {
				return value.name
			})
			 
			console.log(itemList)
			
			uni.showActionSheet({
				itemList: itemList,
				success: (res) => {
					console.log(this.providerList[res.tapIndex].id)
					if(this.providerList[res.tapIndex].id=='qq'){
						this.type=1
					}else{
						this.type=0
					}
					 uni.share({
					 	provider: this.providerList[res.tapIndex].id,
					 	scene: this.providerList[res.tapIndex].type && this.providerList[res.tapIndex].type === 'WXSenceTimeline' ? 'WXSenceTimeline' : "WXSceneSession",
					 	type: this.type,
					 	title:'耘助教',
					 	summary: '耘助教是一个在线教育应用平台',
					 	imageUrl:'http://pds.jyt123.com/wxtest/logo.png',
					 	href:"https://m3w.cn/uniapp",
					 	success: (res) => {
					 		console.log("success:" + JSON.stringify(res));
					 	},
					 	fail: (e) => {
					 		uni.showModal({
					 			content: e.errMsg,
					 			showCancel:false
					 		})
					 	}
					 });
				}
			})
		},
		openLink() {
			plus.runtime.openWeb(this.sourceLink)
		},
	}
}


